	module.exports = {"data":{"markdownRemark":{"html":"<p><img src=\"/static/SumStacker.jpg\"></p>\n<p>Microsoft Game Jame - Sum Stacker!</p>\n<p>The theme of the game jam was education and Sum Stacker taught players how to add quickly.\nBlocks with positive or negative numbers on them are dropped from the sky, and the player had to create rows or columns that added up to 10.\nWhen that happens, that row or column would disappear and the user's score increased.\nThe game ends when player cannot clear fast enough and the blocks overflow from the top.\nWe had a fun time making the game in the span of about 10 hours and the panel of Microsoft judges awarded us third place in the completion.</p>\n<p><strong>Link:</strong><br>\nThe game can be found on our github page <a href=\"https://github.com/krytical/SumStacker\">Sum Stacker</a> and a complied build is available for download at <a href=\"https://github.com/krytical/SumStacker/blob/master/SumStacker/Builds/Builds.rar?raw=true\">Source</a>.</p>\n<p><strong>Team:</strong><br>\nThe team was made up of 4 people (4 developers). </p>\n<p><strong>Work:</strong><br>\nThe game was written using C# and Unity 3D Engine.\nI worked on spawning blocks, block transformation, column sum calculations, and column deletion.</p>\n<p><img src=\"/static/SumStacker2.jpg\"></p>","frontmatter":{"date":"May 24, 2015","path":"/SumStacker","title":"Sum Stacker","image":"/static/SumStacker.jpg"}}},"pathContext":{}}

